import { Component, ViewChild } from '@angular/core';
import { IonicPage, NavController, ModalController, LoadingController, App, AlertController, Slides } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { StorageProvider } from '../../providers/storage/storage';
import { Storage } from "@ionic/storage";
import { ProfilePage } from "../profile/profile";
import { LoadingPage } from "../loading/loading";
import {LoginClassicPage} from "../login/loginClassic";
import { Facebook, FacebookLoginResponse } from '@ionic-native/facebook';

//import {page} from '../register/register';

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {

  introSlides: any;
  profilePage = ProfilePage;
  user: any;
  password: any;
  loaded: boolean;
  isLoggedIn: boolean = false;
  usersFb: any;
  @ViewChild('slides') slides: Slides;

  constructor(
    public navCtrl: NavController, 
    public app: App, 
    public api: Api, 
    public storage: Storage, 
    public loadingCtrl: LoadingController, 
    public alertCtrl: AlertController, 
    public fb: Facebook,
    public storageProvider: StorageProvider,
    public modalCtrl: ModalController) {


this.introSlides = [
      {
        title: '<br/>',
        image: 'assets/img/intro/intro_1.png'
      },
      {
        title: 'Swipe Right to like someone <br /> or Swipe Left to pass',
        image: 'assets/img/intro/intro_2.png'
      },
      {
        title: 'If they also Swipe Right <br /> then "It\'s a Match!"',
        image: 'assets/img/intro/intro_3.png'
      },
      {
        title: 'Only people you\'ve matched <br /> with can message you',
        image: 'assets/img/intro/intro_4.png'
      }
    ]

    this.loaded = false;
    let loading = this.loadingCtrl.create({
      content: 'Chargement...'
    });
    this.storage.get('isConnected').then((val) => {
      if (val) {
        this.navCtrl.setRoot(ProfilePage);
      }
    });
    this.loaded = true;

    this.storage.get('user').then((val) => {
      if (val) {
        this.user = val;
      }
    });

      this.checkLoginFb();
  }

  ionViewDidLoad() {

  }

  goTo(page) {
    this.app.getRootNav().setRoot(page)
    .then(() => {

    })
  }

prepareModal(){
    let profileModal = this.modalCtrl.create(LoginClassicPage, {});
    
    profileModal.present();
  }

  checkLogin(){
    let data: any;
    data = {};
    data.user = this.user;
    data.password = this.password;

    this.api.post('connectUser', data)
    .subscribe(
      (data) => {
        let body: any;
        body = JSON.parse(data.text());

        if(body.error){
          this.showAlertBadAccount(body.message);
        }else{
          this.storage.set('isConnected',true);
          this.storage.set('user', body);
          this.navCtrl.setRoot(LoadingPage);
        }
      },
      (err) => {
        console.log(err);
        this.showAlertBadAccount('Votre compte et\/ou mot de passe ne sont pas valides');
        //alert("ko");
        //this.navCtrl.setRoot(this.navCtrl.getActive().component);
      },
      () => {
        //this.goToHome();
      }
      );
  }

  showAlertBadAccount(message) {
    const alert = this.alertCtrl.create({
      title: 'Erreur de connexion',
      subTitle: message,
      buttons: ['OK']
    });
    alert.present();
  }

  loginFB() {
    console.log('Login FB');
        let data: any;
        data = {};
        this.fb.login(['public_profile', 'email'])
            .then(res => {
                alert(res.authResponse.userID);
                if(res.status === "connected") {
                    data.id = res.authResponse.userID;
                    data.facebookId = res.authResponse.userID;
                    //data.user = this.user;
                    data.password = 'facebook';
                    // Get user infos from the API
                    this.fb.api("/me?fields=name,email", []).then((userFB) => {
                        // Get the connected user details
                        data.usualName = userFB.name;
                        data.email     = userFB.email;
                        this.api.post('connectFacebookUser', data)
                            .subscribe(
                                (data) => {
                                    let body: any;
                                    body = JSON.parse(data.text());
                                    this.storage.set('configurations', body);
                                    alert(body);
                                    // On redirige l'utilisateur vers sa page si il est authentifié
                                    this.storage.set('isConnected',true);
                                    this.storage.set('user', body);

                                    this.navCtrl.setRoot(LoadingPage);
                                },
                                (err) => {
                                    alert(err);
                                    this.storageProvider.clearConnexionStorage();
                                    this.navCtrl.setRoot(LoginPage);
                                },
                                () => {
                                    //this.goToHome();
                                }
                            );
                    });
                } else {
                    this.isLoggedIn = false;
                }
            })
            .catch(e => console.log('Error logging into Facebook', e));
    }

    test(){
        let data: any;
        data = {};
        data.id = 123;
        //data.user = this.user;
        data.password = 'facebook';
        // Get user infos from the API
        // Get the connected user details
        data.usualName = 'facebook';
        data.email     = 'facebook';
        alert('Point 3');
        this.api.post('test', data)
            .subscribe(
                (data) => {
                    alert('Point 4');
                    let body: any;
                    body = JSON.parse(data.text());
                    alert(body);
                    this.storage.set('configurations', body);
                    alert('Point 5');
                },
                (err) => {
                    alert('6');
                    alert(err);
                },
                () => {
                    alert('7');
                    //this.goToHome();
                }
            );
    }

    checkLoginFb(){
        this.fb.getLoginStatus()
            .then(res => {
                if(res.status === "connect") {
                    alert('Point 8');
                    this.isLoggedIn = true;
                    // On redirige l'utilisateur vers sa page si il est authentifié
                    this.navCtrl.setRoot(ProfilePage);
                } else {
                    this.isLoggedIn = false;
                }
            })
            .catch(e => console.log(e));
    }

    getUserDetail(userid) {
        this.fb.api("/"+userid+"/?fields=id,email,name,picture,gender",["public_profile"])
            .then(res => {
                console.log(res);
                this.usersFb = res;
            })
            .catch(e => {
                console.log(e);
            });
    }

}


