import { Component, ViewChild, ViewChildren, QueryList } from '@angular/core';
import { IonicPage, NavController, ModalController, LoadingController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { GameProvider } from '../../providers/game/game';
import {Storage} from "@ionic/storage";
import {PoperProfilePage} from "../poperprofile/poperprofile";
import {LoginPage} from "../login/login";
import 'rxjs/Rx';
import * as _ from 'lodash';

import {
	Direction,
	StackConfig,
	Stack,
	Card,
	ThrowEvent,
	DragEvent,
	SwingStackComponent,
	SwingCardComponent
} from 'angular2-swing';

@IonicPage()
@Component({
	selector: 'page-play',
	templateUrl: 'play.html',
})
export class PlayPage {
	@ViewChild('cardStack') swingStack: SwingStackComponent;
	@ViewChildren('card') swingCards: QueryList<SwingCardComponent>;

	stackConfig: StackConfig;
	popies: any[];
	isLoading: boolean = true;
	userId : number;
	userProfiled: any = {};
	cards: any = this.gameProvider.getCards();

	constructor(public navCtrl: NavController, 
		public storage: Storage,
		public api: Api,
		public modalCtrl: ModalController,
		public loadingCtrl: LoadingController,
		public gameProvider: GameProvider) {
		this.stackConfig = {
			// Default setting only allows UP, LEFT and RIGHT so you can override this as below
			allowedDirections: [
			Direction.LEFT,
			Direction.RIGHT
			],
			throwOutConfidence: (offsetX, offsetY, element) => {
				return Math.min(Math.abs(offsetX) / (element.offsetWidth / 2), 1);
			},
			transform: (element, x, y, r) => {
				this.onItemMove(element, x, y, r);
			},
			throwOutDistance: (d) => {
				return 800;
			}
		};

		this.storage.get('user').then((val) => {
			if (val) {
				this.userId = val.id;
			}
		});

		console.log(this.gameProvider.getCards().length);

		if(this.gameProvider.getCards().length == 0){
			console.log("chargement ");
			this.gameProvider.loadPopies(20);			
		}

	}

	ngAfterViewInit() {
		this.gameProvider.addNewCards(this.popies);
	}

	// Called whenever we drag an element
	onItemMove(element, x, y, r) {
		let nope = element.querySelector('.stamp-nope');
		let like = element.querySelector('.stamp-like');
		let caculatedValue = Math.min(100, Math.abs(x) - 20) / 100;// 0 - 1

		if (x < 0 && Math.abs(x) > 20) {
			nope.style.opacity = caculatedValue;
		} else {
			like.style.opacity = caculatedValue;
		}

		element.style['transform'] = `translate3d(0, 0, 0) translate(${x}px, ${y}px) rotate(${r}deg)`;

		// Zoom effect for the cards underneath
		if (this.swingCards.toArray()[1] != undefined){
			let cardBehind = this.swingCards.toArray()[1].getNativeElement();
			cardBehind.style['transform'] = `scale(${0.94 + 0.06 * caculatedValue}, ${0.94 + 0.06 * caculatedValue})`;
		}
	}


	disliked() {
		let removedCard = this.gameProvider.getCards().shift();

		console.log('You disliked: ' + removedCard.usualName);

		let pop: any;
		pop = {};

		this.api.post('unpop/' + this.userId + '/' + removedCard.id, pop)
		.subscribe(
			(pop) => {
				let body: any;
				body = JSON.parse(pop.text());
			},
			(err) => {
				console.log(err);
				alert("ko");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );

		this.gameProvider.getCardsPlayed().push(removedCard.id);
		this.gameProvider.getMoreCardsIfNecessary();
	}

	liked() {
		let removedCard = this.gameProvider.getCards().shift();
		//this.checkMatching(removedCard);

		console.log('You liked: ' + removedCard.usualName);

		let pop: any;
		pop = {};

		this.api.post('pop/' + this.userId + '/' + removedCard.id, pop)
		.subscribe(
			(pop) => {
				let body: any;
				body = JSON.parse(pop.text());
			},
			(err) => {
				console.log(err);
				alert("ko");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );

		this.gameProvider.getCardsPlayed().push(removedCard.id);
		this.gameProvider.getMoreCardsIfNecessary();
	}

	goToHisProfile(){
		let card = this.cards[0];

		if(card.user.id == this.userProfiled.id){
			// Pas besoin de chargement
			console.log("Pas besoin de chargement");
			this.prepareModal(this.userProfiled);
		}else{

			let data: any;
			data = {};

			let loading = this.loadingCtrl.create({
				spinner: 'crescent',
				content: 'Chargement de données ...',
				dismissOnPageChange: true
			});

			loading.present();

			this.api.post('getUserWithPop/' + card.user.id + '/' + this.userId, data)
			.subscribe(
				(data) => {
					let body: any;
					body = JSON.parse(data.text());

					this.userProfiled = body;
					this.prepareModal(body);

					loading.dismiss();

				},
				(err) => {
					this.storage.remove('user');
					this.storage.remove('isConnected');
					this.navCtrl.setRoot(LoginPage);
					loading.dismiss();
				},
				() => {
                          //this.goToHome();
                      }
                      );
		}
	}

	prepareModal(user){
		let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
		profileModal.onDidDismiss(popiesPlayed => {
			for(let popy in popiesPlayed){

				let found = this.gameProvider.getCards().findIndex(function(element) {
					return element.id = popiesPlayed[popy];
				});

				if(found>-1){
					this.gameProvider.getCardsPlayed().push(this.cards[found].id);
					this.gameProvider.getCards().splice(found, 1);
				}

			}
		});
		profileModal.present();
	}


	trackByFn(index, item) {
		return item.id;
	}

	goTo(page, direction) {
		this.navCtrl.push(page, {}, {
			direction: direction
		});
	}
}



