import { Component } from '@angular/core';
import {
	App, IonicPage, NavController, NavParams, ViewController, AlertController, ActionSheetController,
	LoadingController
} from 'ionic-angular';
import { Api } from '../../providers/api/api';
import { ImagePicker } from '@ionic-native/image-picker';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";
import { Camera, CameraOptions } from '@ionic-native/camera';
import { appSettings } from '../../app/app-settings';

@IonicPage()
@Component({
	selector: 'page-poperprofile',
	templateUrl: 'poperprofile.html',
})
export class PoperProfilePage {

	userProfile: any = {};
	userRank: any = {};
	popies: number[] = [];

	constructor(public navCtrl: NavController,
		public navParams: NavParams,
		public viewCtrl: ViewController,
		public imagePicker: ImagePicker,
		public app: App, 
		public api: Api,
		public storage: Storage,
		public alertCtrl: AlertController,
		public camera: Camera,
		public actionSheetCtrl: ActionSheetController,
		public loadingCtrl: LoadingController) {
		this.userProfile = this.navParams.get("userProfile");
		this.userRank = this.userProfile.rank;
	}

	back() {
		this.viewCtrl.dismiss(this.popies);
	}

	closeModal(){
		console.log("close");
	}

	like(index) {
		if(!this.userProfile.popies[index].popped){
			this.storage.get('user').then((user) => {
				let pop: any;
				pop = {};

				this.api.post('pop/' + user.id + '/' + this.userProfile.popies[index].id, pop)
				.subscribe(
					(pop) => {
						let body: any;
						body = JSON.parse(pop.text());
						this.userProfile.popies[index].popped = true;
						this.popies.push(this.userProfile.popies[index].id);
					},
					(err) => {
						console.log(err);
						alert("ko");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );
			});
		}
	}
}



