import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ModalController, LoadingController } from 'ionic-angular';
import { Api } from '../../providers/api/api';
import {Storage} from "@ionic/storage";
import {LoginPage} from "../login/login";
import {PoperProfilePage} from "../poperprofile/poperprofile";

/**
 * Generated class for the LogPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

 @IonicPage()
 @Component({
  selector: 'page-log',
  templateUrl: 'log.html',
})
 export class LogPage {

  user: any;
  searchOpen:boolean;
  pops: any[];
  search: string;
  searchResult: any[] = [];

  constructor(public navCtrl: NavController, 
    public navParams: NavParams,
    public loadingCtrl: LoadingController,
    public storage: Storage,
    public modalCtrl: ModalController,
    public api: Api) {
    this.storage.get('isConnected').then((val) => {
      if (!val) {
        this.navCtrl.setRoot(LoginPage);
      }
    });

    this.searchOpen = false;

    let loading = this.loadingCtrl.create({
      spinner: 'crescent',
      content: 'Chargement des données ...'
    });

    loading.present();

    let data: any;
    data = {};
    this.storage.get('configurations').then((configurations) => {
      data.range = +configurations.ACTIVITY_RANGE_DAY.value;
    });

    this.storage.get('user').then((user) => {
      this.user = user;
      this.api.post('getLastPops/' + user.id, data)
      .subscribe(
        (data) => {
          this.pops= [];

          let body: any;
          body = JSON.parse(data.text());

          for(let i=0; i<body.length;i++){
            this.pops[i]=body[i];
          }

          console.log(this.pops);

          loading.dismiss();
        },
        (err) => {
          loading.dismiss();
          this.storage.remove('user');
          this.storage.remove('isConnected');
          this.navCtrl.setRoot(LoginPage);
        },
        () => {
                          //this.goToHome();
                        }
                        );

    });
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad LogPage');
  }


  filterUsers(event){
    if(this.search.length >= 3){

      let data: any;
      data = {};
      data.search = this.search;

      this.api.post('searchUsers/' + this.user.id, data)
      .subscribe(
        (data) => {
          let body: any;
          body = JSON.parse(data.text());

          this.searchResult = body;
        },
        (err) => {
          this.storage.remove('user');
          this.storage.remove('isConnected');
          this.navCtrl.setRoot(LoginPage);
        },
        () => {
                          //this.goToHome();
                        }
                        );
    }else{
      this.searchResult = [];
    }
  }

goToHisProfile(userId){

      let data: any;
      data = {};

      let loading = this.loadingCtrl.create({
        spinner: 'crescent',
        content: 'Chargement de données ...',
        dismissOnPageChange: true
      });

      loading.present();

      this.api.post('getUserWithPop/' + userId + '/' + this.user.id, data)
      .subscribe(
        (data) => {
          let body: any;
          body = JSON.parse(data.text());

          this.prepareModal(body);

          loading.dismiss();

        },
        (err) => {
          this.storage.remove('user');
          this.storage.remove('isConnected');
          this.navCtrl.setRoot(LoginPage);
          loading.dismiss();
        },
        () => {
                          //this.goToHome();
                      }
                      );
    
  }

  prepareModal(user){
    let profileModal = this.modalCtrl.create(PoperProfilePage, { userProfile: user });
    profileModal.present();
  }

  goTo(page, direction) {
    this.navCtrl.push(page, {}, {
      direction: direction
    });
  }
}



