import { Injectable } from '@angular/core';
import { appSettings } from '../../app/app-settings';
import { Api } from '../api/api';
import { Storage } from "@ionic/storage";

@Injectable()
export class GameProvider {
	private url = appSettings.API_URL;
	private apiToken = appSettings.API_TOKEN; 

	user: any;
	cardsPlayed: any[] = [];
	cards: any[] = [];

	constructor(public api: Api,
		public storage: Storage) {
	}

	loadPopies(count) {
		this.storage.get('user').then((val) => {
			if (val) {
				this.user = val;
				this.addPopies(count, val.id);
			}
		});
	}

	addPopies(count, userId){
		let popies: any;
		popies = {};
		popies.reject= [];
		for(let card in this.cardsPlayed){
			popies.reject.push(this.cardsPlayed[card]);
		}
		for(let card in this.cards){
			popies.reject.push(this.cards[card].id);
		}		

		let newPopies: any[];
		newPopies = [];
		this.api.post('getGamePopies/' + this.user.id + '/' + count, popies)
		.subscribe(
			(popies) => {
				let body: any;
				body = JSON.parse(popies.text());
				for(let popy in body){
					newPopies.push({
						id: body[popy]['id'],
						image: body[popy]['image'],
						user: {
							usualName : body[popy]['user']['usualName'],
							accountImage : body[popy]['user']['accountImage'],
							id : body[popy]['user']['id']
						}
					});
				}
				this.addNewCards(newPopies);
			},
			(err) => {
				console.log(err);
				alert("kos");
                  //this.navCtrl.setRoot(this.navCtrl.getActive().component);
              },
              () => {
                  //this.goToHome();
              }
              );
	}

	// Add new cards to our array
	addNewCards(popies) {
		this.cards.push(...popies);
	}


	getMoreCardsIfNecessary() {
		if (this.cards.length < 10) {
			this.storage.get('user').then((val) => {
				if (val) {
					this.addPopies(10, val.id);
				}
			});
		}
	}

	getCards(){
		return this.cards;
	}

	getCardsPlayed(){
		return this.cardsPlayed;	
	}

}



