import { Injectable } from '@angular/core';
import { appSettings } from '../../app/app-settings';
import { Api } from '../api/api';
import { Storage } from "@ionic/storage";

@Injectable()
export class StorageProvider {
	private url = appSettings.API_URL;
	private apiToken = appSettings.API_TOKEN; 

	user: any;

	constructor(public api: Api,
		public storage: Storage) {
	}

	loadConfigurations() {
		let data: any;
		data = {};
		data.lang= 'fr';

		this.api.post('getConfiguration', data)
		.subscribe(
			(data) => {
				let body: any;
				body = JSON.parse(data.text());

				this.storage.set(appSettings.STORAGE_ATTRIBUTES.CONFIGURATIONS, body);


			},
			(err) => {
				this.clearConnexionStorage();
			},
			() => {
                          //this.goToHome();
                      }
                      );
	}

	clearConnexionStorage(){
		this.storage.remove(appSettings.STORAGE_ATTRIBUTES.CONFIGURATIONS);
		this.storage.remove(appSettings.STORAGE_ATTRIBUTES.USER);
		this.storage.remove(appSettings.STORAGE_ATTRIBUTES.FROM_LOADING);
		this.storage.remove(appSettings.STORAGE_ATTRIBUTES.IS_CONNECTED);		
	}
}



