import { Injectable } from '@angular/core';
import { appSettings } from '../../app/app-settings';
import { Api } from '../api/api';
import { Storage } from "@ionic/storage";

@Injectable()
export class Users {
	private url = appSettings.API_URL;
	private apiToken = appSettings.API_TOKEN; 

	user: any;

	constructor(public api: Api,
		public storage: Storage) {
	}

	loadUser() {
		let result: any = {};

		this.storage.get('user').then((user) => {
			let data: any;
			data = {};

			this.api.post('getUser/' + user.id, data)
			.subscribe(
				(data) => {
					let body: any;
					body = JSON.parse(data.text());

					this.storage.set('user', body);
					this.storage.set('fromLoading', true);

					this.user = body;

					console.log(this.user);

				},
				(err) => {
					this.storage.remove('configurations');
					this.storage.remove('user');
					this.storage.remove('isConnected');
					this.storage.remove('fromLoading');
				},
				() => {
                          //this.goToHome();
                      }
                      );
		});

	}
}



